#about {
    padding-left: 15%;
    padding-right: 20%; 
    min-height: 70vh; 
}

.about-content {
    display: flex;
    flex-direction: row;    
    justify-content: space-around;
}
  
.about-description {
    font-family: "FiraCode";
    color: var(--gray);
    max-width: 600px;
    font-size: 20px;
    text-align: justify;
    margin: 0;
    height: 50vh;
}

.about-description a,
.about-description b {
  font-size: 21px;
  font-weight: bold;
}

.about-image {
   padding-left: 40px;   
}
  
.about-image img {
    max-width: 300px;
    height: auto;
    border-radius: 10px;
    margin-left: 30px;
    box-shadow: 6px 13px 23px -5px rgba(0, 0, 0, 0.46);
    border: none;
}
  

.about-description ul {
    padding-top: 20px;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(4, minmax(130px, 10px));
}  
.about-description li {
position: relative;
/* padding-left: 20px; */
font-size: 14px;
color: var(--slate);
display: flex;
flex-direction: column;
align-items: center;
}
  
.about-description li::before {
content: "";
color: var(--gray);
position: absolute;
left: 0;
}

.about-description li img {
    width: 24px;
    height: 24px;
}  

  